<template>
  <div class="gradient">
    <div class="own-container">
      <!--
      https://github.com/zwhGithub/vue-calendar
       // v-on:isToday="clickToday"
      // :markDate=arr // arr=['2018/4/1','2018/4/3'] 标记4月1日和4月3日 简单标记
      //:markDateMore=arr // 多种不同的标记
      // 第一个标记和第二个标记不能同时使用
      // :agoDayHide='1514937600' //某个日期以前的不允许点击  时间戳10位
      // :futureDayHide='1525104000' //某个日期以后的不允许点击  时间戳10位
      // :sundayStart="true" //默认是周一开始 当是true的时候 是周日开始
      -->
      <!--<div>
        <Calendar
                @choseDay="clickDay"
                @changeMonth="changeDate"
                :markDateMore="learnDays">
        </Calendar>
      </div>-->

      <!--      <van-icon class="iconfont icon-qiyeyunyingfenxi" />
            <i class="iconfont icon-qiyeyunyingfenxi"></i>-->
      <div class="own-panel" v-if="list[0].items">
        <van-pull-refresh
                v-model="list[0].refreshing"
                @refresh="onRefreshNew">
          <van-list
                  v-model="list[0].loading"
                  :finished="list[0].finished"
                  finished-text="没有更多了"
                  :error.sync="list[0].error"
                  error-text="请求失败，点击重新加载"
                  @load="onLoadNew">
              <van-cell class="onw-list-row" v-for="item in list[0].items" :key="item._id" @click="goRead(item)">
                  <van-row>
                  <van-col span="19">
                    <div class="article-title">
                      {{item.article_title}}
                    </div>
                    <div class="article-content own-ellipsis">
                      {{item.article_brief}}
                    </div>
                    <van-row>
                      <van-col span="6">难度：</van-col>
                      <van-col span="18">
                        <van-rate v-model="item.article_grade+1"/>
                      </van-col>
                    </van-row>
                    <van-row type="flex" justify="space-around" gutter="6">
                      <!-- <van-col span="12">
                         &lt;!&ndash;<van-tag plain>字数: <span>300</span>个</van-tag>&ndash;&gt;
                         <van-tag style="font-size: 3.2vw" plain>价格：<span>{{item.pay_price}}</span></van-tag>
                       </van-col>
                       <van-col span="12">
                       </van-col>-->
                      <!--<van-col span="6">价格：</van-col>
                      <van-col span="18">
                        <span>{{item.pay_price}} 元</span>
                      </van-col>-->
                    </van-row>
                  </van-col>
                  <van-col span="5">
                    <div class="read-box">
                      <img src="../assets/img/read.png" alt="">
                    </div>
                    <!--<div class="cnn-btn" @click="goRead(item)">
                      学 习
                    </div>-->
                  </van-col>
                </van-row>
              </van-cell>
          </van-list>
        </van-pull-refresh>
      </div>
      <div class="own-panel" v-else>暂无学习内容</div>
    </div>
    <!--创建订单后弹出-->
    <van-dialog
            v-model="showPayBox"
            title="提 示"
            confirm-button-text="支付"
            @confirm="submitPay"
            confirmButtonColor="#3dcc97"
            show-cancel-button>
      <!--<img src="https://img.yzcdn.cn/1.jpg">-->
      <div class="own-dialog-text">
        该产品属于虚拟产品，一经购买，不支持退款或更换，请按需购买。
      </div>
    </van-dialog>

    <!--取消支付后弹出-->
    <!--<van-dialog
            v-model="cancelPayShow"
            title="标题"
            show-cancel-button
    >
      <img src="https://img.yzcdn.cn/1.jpg">
    </van-dialog>-->

    <!--取消支付后弹出-->
    <Confirm-dialog :confirmDialog="confirmOptions" @closeShow="closeConfirmDialog"></Confirm-dialog>

    <!--创建订单后弹出-->
    <Order-dialog :confirmDialog="createOrderOptions"></Order-dialog>

    <Float-btn text="复习" @onFloatBtnClicked="goLearned"></Float-btn>
  </div>
</template>

<script>
  import ConfirmDialog from '@/components/ConfirmDialog.vue'
  import OrderDialog from '@/components/OrderDialog.vue'
  import FloatBtn from '@/components/FloatBtn.vue'
  import {NoticeBar, Panel, Skeleton} from 'vant';
  import {mapGetters} from "vuex";
  import Calendar from 'vue-calendar-component';

  export default {
    name: 'articleList',
    components: {
      ConfirmDialog,
      OrderDialog,
      [NoticeBar.name]: NoticeBar,
      [Skeleton.name]: Skeleton,
      [Panel.name]: Panel,
      Calendar,
      FloatBtn
    },
    computed: {
      ...mapGetters(["userInfo"])
    },
    created() {
    },
    async mounted() {
      this.params.openId = this.userInfo.openid;
      this.orderParams.openid = this.userInfo.openid;
      this.payParams.openId = this.userInfo.openid;
      // this.wxShare.share()
    },
    data() {
      return {
        learnDays: [],
        createOrderOptions: {
          show: false,
          title: '提 示',
          msg: ' 该产品属于虚拟产品，一经购买，不支持退款或更换，请按需购买。',
          cancelText: '取 消',
          confirmText: '立即支付',
          cancelAction: () => {
            this.createOrderOptions.show = false;
          },
          confirmAction: () => {
            this.submitPay();
          }
        },
        confirmOptions: {
          show: false,
          title: '还在犹豫？',
          msg: '联系督学班长了解下？',
          cancelText: '重新支付',
          confirmText: '跟她聊聊',
          cancelAction: () => {
            this.submitPay();
          },
          confirmAction: () => {
            this.$router.replace('/groupCode')
          }
        },
        orderParams: {
          article: '',  // 5cd245a1ea21fd0efceed74b
          openid: '', // oAfgA1QTrvWbYXZ4iU2sGKMLEs5g
          pay_type: 1
        },
        payParams: {
          orderId: '',
          openId: ''
        },
        cancelPayShow: false,
        showPayBox: false,
        params: {
          page: 1,
          limit: 5,
          openId: ''
        },
        active: 0,
        ownBarClass: 'own-nav-b',
        list: [{
          items: [],
          refreshing: false,
          loading: false,
          error: false,
          finished: false
        },
          {
            items: [],
            refreshing: false,
            loading: false,
            error: false,
            finished: false
          }
        ]
      }
    },
    methods: {
      goLearned() {
        this.$router.push('/learned');
      },
      clickDay(data) {
        console.log(data); //选中某天
      },
      changeDate(data) {
        console.log(data); //左右点击切换月份
      },
      clickToday(data) {
        console.log(data); // 跳到了本月
      },
      closeConfirmDialog(val) {

      },
      goRead(item) {
        console.log(item.id)
        this.loadingGoArticle(item.id);
      },
      submitPay() {
        this.createOrderOptions.show = false;
        this.confirmOptions.show = false;
        this.apis.order.articleDoPay(this, false, this.payParams).then(res => {
          this.$wx.chooseWXPay({
            timestamp: res.data.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
            nonceStr: res.data.nonceStr, // 支付签名随机串，不长于 32 位
            package: res.data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
            signType: res.data.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
            paySign: res.data.paySign, // 支付签名
            success: (res) => {
              // 支付成功后的回调函数
              this.loadingGoArticle(this.payParams.orderId);
            },
            // 支付取消回调函数
            cancel: (res) => {
              console.log('用户取消支付~')
              // this.cancelPayShow = true;
              this.confirmOptions.show = true;
            },
            // 支付失败回调函数
            fail: (res) => {
              console.log('支付失败~')
            }
          })
        })
      },
      buyNow(item) {
        // this.loadingGoArticle()
        if (this.orderParams.article && item.id === this.orderParams.article) {
          this.createOrderOptions.show = true;
        } else {
          this.orderParams.article = item.id;
          this.orderParams.openid = this.params.openId;
          // 创建订单
          this.apis.order.createArticleOrder(this, false, this.orderParams).then(res => {
            // this.showPayBox = true;
            this.createOrderOptions.show = true;
            this.payParams.orderId = res.data.id;
            this.payParams.openId = res.data.openid;
          })
        }
      },
      // 跳转到文章详情 不包含文章解析
      loadingGoArticle(articleId) {
        this.$router.push({
          name: 'article',
          params: {
            id: articleId
          }
        })
      },
      changeTab() {
        this.params.page = 1;
      },
      onLoadNew() {
        this.apis.article.getArticleList(this.params).then(res => {
          if(res.code === 200) {
            this.list[0].items = this.list[0].items.concat(res.data.docs);
            this.list[0].loading = false;
            this.params.page++;
            if (this.list[0].items.length >= res.data.total) {
              this.list[0].finished = true;
            }
          } else {
            this.list[0].error = true;
          }

        });
      },
      onRefreshNew() {
        setTimeout(() => {
          this.list[0].items = [];
          this.list[0].error = false;
          this.list[0].finished = false;
          this.list[0].refreshing = false;
          this.params.page = 1;
          window.scrollTo(0, 10);
        }, 1000)
      }
    }

  }
</script>
<style lang="css" scoped>
  .own-container {
    padding: 10px 0;
  }

  .own-page-title {
    font-size: 4.2vw;
    font-weight: 600;
  }

  .own-panel {
    margin-top: 10px;
    margin-bottom: 60px;
    width: 100%;
    padding: 10px;
    background: #fff;
    border-radius: 4px;
  }

  .own-panel-title {
    width: 100%;
    height: 5vw;
    font-weight: 600;
    line-height: 4vw;
    text-align: center;

  }

  .article-title {
    width: 90%;
    height: 6vw;
    font-size: 4vw;
    line-height: 6vw;
    text-align: left;
    font-weight: 500;
    overflow: hidden;
    white-space: nowrap;
    text-overflow:ellipsis;
  }

  .article-content {
    width: 100%;
    height: 10vw;
    font-size: 3.2vw;
  }

  .own-ellipsis {
    width: 100%;
    height: 11.6vw;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .own-dialog-text {
    padding: 22px 20px;
    line-height: 6vw;
    color: #fd7020;
    font-size: 4vw;
  }

  .own-tab-title {
    font-size: 4.2vw;
  }

  .article-bottom {
    /*text-align: right;*/
  }

  /*  .page {
      position: absolute;
      left:0;
      top:0;
      width: 100vw;
      height:100vh;
    }*/
  .onw-list-row {
    /*height: 34vw;*/
  }

  .cnn-btn {
    text-align: center;
    font-size: 5vw;
  }
  .read-box {
    margin-top: 5vw;
    margin-left: 6vw;
    width: 11vw;
    height: 11vw;
  }
  .read-box img{
    width: 100%;
  }
</style>